"use client"

import {
  AlertDialog,
  AlertDialogContent,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogDescription,
} from "@/components/ui/alert-dialog"
import { Loader2 } from "lucide-react"
import { useEffect, useState } from "react"

interface ScrapingDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
}

export function ScrapingDialog({ open, onOpenChange }: ScrapingDialogProps) {
  const [progress, setProgress] = useState(0)

  // Simulate progress to keep the user engaged during the long wait
  useEffect(() => {
    if (!open) {
      setProgress(0)
      return
    }

    const interval = setInterval(() => {
      setProgress((prev) => {
        if (prev >= 90) return prev // Stall at 90% until actually done
        return prev + 1
      })
    }, 2000) // Slow progress over ~3 minutes

    return () => clearInterval(interval)
  }, [open])

  return (
    <AlertDialog open={open} onOpenChange={onOpenChange}>
      <AlertDialogContent className="max-w-[400px] sm:max-w-[500px]">
        <AlertDialogHeader className="space-y-4">
          <div className="flex flex-col items-center justify-center gap-4 py-4">
            <div className="relative flex h-16 w-16 items-center justify-center">
              <Loader2 className="h-16 w-16 animate-spin text-[#0dadb7] opacity-20" />
              <Loader2 className="absolute h-16 w-16 animate-spin text-[#0dadb7]" style={{ animationDuration: "3s" }} />
              <div className="absolute inset-0 flex items-center justify-center text-xs font-medium">
                {progress}%
              </div>
            </div>
            <AlertDialogTitle className="text-center text-xl">Analyzing Content</AlertDialogTitle>
            <AlertDialogDescription className="text-center">
              We're scraping and analyzing your reference pages. This usually takes 2-3 minutes to ensure we capture all the details.
            </AlertDialogDescription>
          </div>
          
          <div className="space-y-2">
            <div className="h-2 w-full overflow-hidden rounded-full bg-secondary">
              <div 
                className="h-full bg-[#0dadb7] transition-all duration-500 ease-out" 
                style={{ width: `${progress}%` }} 
              />
            </div>
            <p className="text-center text-xs text-muted-foreground animate-pulse">
              Extracting USPs, tone of voice, and key benefits...
            </p>
          </div>
        </AlertDialogHeader>
      </AlertDialogContent>
    </AlertDialog>
  )
}

