"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Label } from "@/components/ui/label"
import { Input } from "@/components/ui/input"
import { Link, Loader2, Plus, X } from "lucide-react"
import { StepTwoState } from "@/lib/types"
import { ScrapingDialog } from "@/components/scraping-dialog"
import { useState } from "react"

interface StepTwoProps {
  data: StepTwoState
  updateData: (data: StepTwoState) => void
  onNext: () => void
  onBack: () => void
  isLoading: boolean
}

export function StepTwo({ data, updateData, onNext, onBack, isLoading }: StepTwoProps) {
  const [showDialog, setShowDialog] = useState(false)

  const handleNext = () => {
    // In a real implementation, this would trigger the backend process
    // For now, we show the dialog and simulate the delay if isLoading is managed by parent
    // But since we want the dialog to control the UX now:
    setShowDialog(true)
    
    // Simulate the parent's "onNext" being the start of the process
    onNext() 
  }

  const addUrlField = () => {
    updateData({ ...data, referenceUrls: [...data.referenceUrls, ""] })
  }

  const removeUrlField = (index: number) => {
    updateData({
      ...data,
      referenceUrls: data.referenceUrls.filter((_, i) => i !== index),
    })
  }

  const updateUrl = (index: number, value: string) => {
    const updated = [...data.referenceUrls]
    updated[index] = value
    updateData({ ...data, referenceUrls: updated })
  }

  return (
    <div className="space-y-6">
      {/* Reference Pages Section */}
      <Card>
        <CardHeader className="pb-4">
          <div className="flex items-center gap-2">
            <Link className="h-5 w-5 text-muted-foreground" />
            <CardTitle className="text-base">Reference Pages</CardTitle>
          </div>
          <CardDescription>
            Add URLs to existing pages (product pages, competitor pages) and we'll extract key information. You can add
            multiple references.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {data.referenceUrls.map((url, index) => (
              <div key={index} className="flex items-center gap-2">
                <div className="flex-1 space-y-1">
                  <Label htmlFor={`url-${index}`} className="text-sm text-muted-foreground">
                    Reference URL {index + 1}
                  </Label>
                  <Input
                    id={`url-${index}`}
                    type="url"
                    placeholder="https://example.com/product-page"
                    value={url}
                    onChange={(e) => updateUrl(index, e.target.value)}
                  />
                </div>
                {data.referenceUrls.length > 1 && (
                  <Button
                    variant="ghost"
                    size="icon"
                    className="mt-6 text-muted-foreground hover:text-destructive"
                    onClick={() => removeUrlField(index)}
                  >
                    <X className="h-4 w-4" />
                    <span className="sr-only">Remove URL</span>
                  </Button>
                )}
              </div>
            ))}
            <Button variant="outline" size="sm" className="gap-2 bg-transparent" onClick={addUrlField}>
              <Plus className="h-4 w-4" />
              Add another URL
            </Button>
            <p className="text-xs text-muted-foreground">
              We'll scrape these pages to extract USPs, pricing, tone of voice, and more.
            </p>
          </div>
        </CardContent>
      </Card>

      {/* Navigation */}
      <div className="flex justify-between pt-4">
        <Button variant="outline" onClick={onBack}>
          Back
        </Button>
        <Button onClick={handleNext} disabled={isLoading} size="lg">
          {isLoading ? (
            <>
              <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              Starting...
            </>
          ) : (
            "Scrape & Continue"
          )}
        </Button>
      </div>
      
      <ScrapingDialog open={isLoading} onOpenChange={() => {}} />
    </div>
  )
}
